export const mixins = {
    computed: {
        tabLength() {
            return document.getElementsByClassName('van-tab').length
        }
    },
    methods: {
        onSwipeLeft() {
            if (this.active < this.tabLength - 1) {
                this.active = this.active + 1
            }
        },
        onSwipeRight() {
            if (this.active > 0) {
                this.active = this.tabLength - 2
            }
        }
    }
}